.pomodoro{
    height: 100vh;
    width: 50vw;
    max-width: 50vw;
    max-height: 100vh;
    display: flex;
    flex-direction: column;
    overflow: hidden;
    gap: 20px;
    padding: 10px;
    position: relative;
    box-sizing: border-box;
}
.top{
    width: 100%;
    height: 50px;
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 10px;
    margin-top: auto;
}
.minimize-button{
    position: absolute;
    top: 20px;
    left: 20px;
    background-color: transparent;
    border: none;
}
.timer{
    height: calc(100% - 70px);
    max-height: 100%;
    width: 100%;
    max-width: 50vw;
    aspect-ratio: 1 / 1;
    display: flex;
    align-items: center;
    justify-content: center;
    grid-row: 1 / 2;
    overflow: hidden;
}
.timer-background{
    background-color: white;
    width: 100%;
    height: 100%;
    aspect-ratio: 1 / 1;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
}
.timer-content{
    width: calc(100% - 10px);
    height: calc(100% - 10px);
    background-color: #171717;
    border-radius: 50%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: 10px;
}
:global(.App.amoled) .timer-content{
    background-color: black;
}
.timer-content .time{
    font-size: 50px;
}
.timer-content button{
    width: 75px;
    height: 40px;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 5px;
    background-color: #0F0F0F;
}
:global(.App.amoled) .buttons button{
    background-color: black;
    border: 1px solid rgba(255, 255, 255, 0.5);
}

.timer-background.animated-session-end {
    animation: flashGradient 0.5s infinite alternate ease-in-out;
}

@keyframes flashGradient {
    0% {
        background: red;
    }
    50% {
        background: white;
    }
    100% {
        background: red;
    }
}

.total-time{
    position: sticky;
    bottom: 0;
    right: 0;
    font-size: 0.8rem;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    width: 100%;
    padding: 10px 0;
    border-top: 1px solid rgba(211, 211, 211, 0.1);
    background-color: #171717;
}
.total-time p:nth-child(2){
    opacity: 0.5;
}
.sessions-counter{
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 5px;
}
.current-section-counter{
    color: #ff4500 !important;
}


.buttons{
    width: 100%;
    height: 50px;
    display: flex;
    flex-wrap: wrap;
    gap: 5px;
    grid-row: 2 / 3;
    padding: 0 10px;
}
.buttons button{
    height: 50px;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: #0F0F0F;
}
.buttons .small-button {
    width: calc((100% / 4) - 10px);
    border: 1px solid transparent;
    transition: transform 0.1s ease-in-out, border-color 0.1s ease-in-out;
}

.buttons .small-button:active {
    transform: scale(0.95);
    border-color:rgba(211, 211, 211, 0.3);
}

.buttons .small-button img{
    width: 30px;
    height: 30px;
}
.settings-button{
    position: absolute;
    right: 15px;
    top: 15px;
    background-color: transparent;
    border: none;
}
.settings-button img{
    height: 40px;
    width: 40px;
}








/* settings */

.settings{
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    gap: 10px;
    position: absolute;
    right: 0;
    bottom: 0;
    background-color: #171717;
    z-index: 20;
    padding: 10px;
    animation-name: showPomodoroSettings;
    animation-duration: 0.1s;

}
.settings-container{
    width: 100%;
    height: auto;
    display: flex;
    flex-direction: column;
    gap: 10px;
    overflow-y: auto;
}
@keyframes showPomodoroSettings {
    from{
        right: -100%;
        opacity: 0;
    }
    to{
        right: 0;
        opacity: 1;
    }
    
}
.settings .header{
    width: 100%;
    height: 50px;
    display: flex;
    align-items: center;
}
.settings .header h2{
    margin-right: auto;
}
.settings .header button{
    background-color: transparent;
    border: none;
}
.settings-section{
    width: 100%;
    display: flex;
    flex-direction: column;
    gap: 5px;
}
.settings-section .top p{
    width: auto;
    display: flex;
    align-items: center;
    justify-content: start;
}
.settings-section .top{
    display: flex;
    gap: 10px;
    align-items: center;
    justify-content: start;
}
.settings-section .top input,
.settings-section option,
.settings-section select{
    width: 50px;
    height: 30px;
    border: none;
    border-radius: 5px;
    padding-left: 10px;
    color: black;
}

/* minimized timer */

.minimized-pomodoro{
    width: 60px;
    height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    border-left: 1px solid rgba(221, 221, 221, 0.2);
    justify-content: center;
    gap: 10px;
    background: linear-gradient(to top, #FF8C00 var(--progress), transparent var(--progress));
}
.minimized-pomodoro .info{
    width: 50px;
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 10px;
    text-align: center;
    background-color: #171717;
    border-radius: 5px;
    padding: 10px 0;
    border: 1px solid transparent;
}
.minimized-pomodoro .minimized-time{
    width: 100%;
    display: flex;
    flex-direction: column;
    gap: 5px;
}
.minimized-pomodoro .minimized-time p{
    font-size: 25px;
}
.minimized-pomodoro .isFinished .minimized-time p{
    animation: flashText 0.5s infinite alternate ease-in-out;
}
@keyframes flashText {
    0% {
        color: red;
    }
    50% {
        color: white;
    }
    100% {
        color: red;
    }
}
.minimized-pomodoro .sessions-counter{
    gap: 2px;
}
.maximize-button{
    width: 40px;
    height: 40px;
    background-color: transparent;
    border: none;
}
.minimized-pomodoro .buttons{
    width: 100%;
    height: auto;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 0;
    padding-bottom: 15px;
}
.minimized-pomodoro .buttons button{
    width: 50px;
    height: 50px;
}




/* media queries */

@media (orientation: landscape) {
    
    .timer{
        height: 100%;
        width: auto;
        aspect-ratio: 1 / 1;
    }
    .timer-background{
        width: auto;
        min-width: calc(100vh - 150px);
        height: 100%;
    }
    .pomodoro{
        height: 100vh;
        max-width: 50vw;
    }

}
@media (orientation: portrait) {
    .pomodoro{
        height: 50vh;
        width: 100vw;
        max-width: 100vw;
        max-height: 50vh;
        border-top: 1px solid rgba(211, 211, 211, 0.1);
    }
    .buttons{
        width: 100%;
        height: 75px;
        justify-content: center;
    }
    .buttons .small-button{
        width: 50px;
        height: 50px;
    }
    .timer{
        height: calc(50vh - 75px);
        max-height: calc(50vh - 75px);
        width: 100vw;
        max-width: 100vw;
    }
    .timer-background{
        height: 100%;
        max-height: 100%;
        width: auto;
    }
    .minimized-pomodoro{
        height: 60px;
        width: 100vw;
        flex-direction: row;
    }
    .minimized-pomodoro .buttons{
        flex-direction: row;
        padding: 0;
    }
    .minimized-pomodoro .minimized-time{
        flex-direction: row;
        padding: 0 10px;
        width: auto;
    }
    .minimized-pomodoro .maximize-button{
        margin: 0 15px;
    }
    .minimized-pomodoro{
        background: linear-gradient(to right, #FF8C00 var(--progress), transparent var(--progress));
    }
}